import { Heading,Box, HStack,Text} from "@chakra-ui/react";
const Header = () => {
  const Links=[
    {link:"Home"},
    {link:"About"},
    {link:"Projects"},
    {link:"Contact"}
  ]
    
  return (
    <>
      <Box backgroundColor="#000000" margin="0 auto">
        <HStack
          px={16}
          py={2}
          justifyContent="space-between"
          alignItems="center">
          <Heading gap={2} display="flex" fontFamily="Antipasto" as="h1" size="lg">
              <Text>Mohamed</Text><Text>Safouen</Text>
          </Heading>
          <nav>
            <HStack spacing={20}>
              {Links.map((x, index) => {
                return (
                  <a
                    key={index}
                    href={`#${x.link}`}
                    style={{ fontSize: 16, color: "#a9a9a9" }}>
                    {x.link}
                  </a>
                );
              })}
            </HStack>
          </nav>
        </HStack>
      </Box>
    </>
  );
};
export default Header;
